.keyboard-holder {
    position: relative;
    min-height: 500px;
    background-color: #271768;

    .keyboard {
        background-color: #0d0f3d;
        position: absolute;
        max-width: 270px;
        border-radius: 50px 50px 50px 50px;
        max-height: 250px;
        margin: auto;
        left: 0;
        bottom: 0;
        right: 0;
        top: 0;
        background-repeat: no-repeat;
        background-image: 
        radial-gradient(circle at -9090%, #002155 99%, transparent 23%), 
        radial-gradient(circle at 50% -9120%, #002155 99%, transparent 97%, transparent 36%), 
        radial-gradient(circle at 9090%, #002155 99%, transparent 23%), 
        radial-gradient(circle at 50% 9120%, #002155 99%, transparent 23%);

        &:after {
            content: '';
            height: 10px;
            width: 10px;
            border-radius: 50%;
            background-color: green;
            position: absolute;
            bottom: 5px;
            right: 40px;
            box-shadow: -16px 0px 0 red, -34px 0px yellow;
            
        }

        &:before {
            content: '';
            color: green;
            white-space: pre;
            font-weight: 600;
            font-family: monospace;
            font-size: large;
            position: absolute;
            top: 44px;
            left: 42px;
            animation: hi-neo 8s infinite;
        }
    }

    @keyframes hi-neo {
        0% {
            content: '';
        }
        5% {
            content: 'H';
        }
        10% {
            content: 'Hi'
        }
        15% {
            content: 'Hi,';
        }
        20% {
            content: 'Hi, N';
        }
        25% {
            content: 'Hi, Ne';
        }
        30% {
            content: 'Hi, Neo';
        }
        35% {
            content: 'Hi, Neo .';
        }
        45% {
            content: 'Hi, Neo . .';
        }

        50% {
            content: 'Hi, Neo . . . \A o';
        }
        55% {
            content: 'Hi, Neo . . . \A op';
        }
        60% {
            content: 'Hi, Neo . . . \A ope';
        }
        65% {
            content: 'Hi, Neo . . . \A open';
        }
        70% {
            content: 'Hi, Neo . . . \A open t';
        }
        75% {
            content: 'Hi, Neo . . . \A open th';
        }
        80% {
            content: 'Hi, Neo . . . \A open the';
        }
        85% {
            content: 'Hi, Neo . . . \A open the d';
        }
        90% {
            content: 'Hi, Neo . . . \A open the do';
        }
        95% {
            content: 'Hi, Neo . . . \A open the doo';
        }
        100% {
            content: 'Hi, Neo . . . \A open the door ';
        }
        
    }
}